import React from 'react';
import {Icon} from 'antd';
import Hicon from '../Hicon';
import * as tool from '../../utils/tool';
import Logger from '../../utils/logger';
import Decorate from '../../utils/decorate';
import _config from './editorConfig';

import './Editor.styl';

class Editor extends React.Component {
  handleDecorate (type, option = {}) {

  }
  render () {
    return (
      <div className='h-editor'>
        <div className="h-editor-navigation">
          <div className="h-editor-navigation-nav">
            <div className="h-editor-button-wrap">
              <span className="h-editor-button" title="Bold" onClick={() => this.handleDecorate('bold')}><Icon type="bold" /></span>
              <span className="h-editor-button" title="Italic" onClick={() => this.handleDecorate('italic')}><Icon type="italic" /></span>
              <span className="h-editor-button" title="Underline" onClick={() => this.handleDecorate('underline')}><Icon type="underline" /></span>
              <span className="h-editor-button" title="Strikethrough" onClick={() => this.handleDecorate('strikethrough')}><Icon type="strikethrough" /></span>
              <span className="h-editor-button" title="Unordered list" onClick={() => this.handleDecorate('unordered')}><Icon type="ordered-list" /></span>
              <span className="h-editor-button" title="Ordered list" onClick={() => this.handleDecorate('order')}><Icon type="unordered-list" /></span>
              <span className="h-editor-button" title="Quote" onClick={() => this.handleDecorate('quote')}><Icon type="icon-quote-left" /></span>
              <span className="h-editor-button" title="Line break" onClick={() => this.handleDecorate('hr')}><Icon type="icon-window-minimize" /></span>
              <span className="h-editor-button" title="Inline code" onClick={() => this.handleDecorate('inlinecode')}><Icon type="icon-embed" /></span>
              <span className="h-editor-button" title="Block code" onClick={() => this.handleDecorate('code')}><Icon type="icon-embed2" /></span>
            </div>
          </div>
          <div className="h-editor-navigation-nav">
            <div className="h-editor-button-wrap">
              <Hicon type="icon-full" className="h-editor-button"/>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Editor;